import * as d3 from 'd3'
import difangzhengf from '../images/difangzhengfu.png'
import guowu from '../images/guowu.png'
import waijiaojigou from '../images/waijiaojigou.png'
import { Frequency1, countryData, regionalData } from '../data'
import { rgb } from 'd3'
const filterValue=3

const titles=['外交机构在回应什么','国务院其他组成部门在回应什么？',"地方政府机构回应什么？"]
const titlesLength=[260,370,290]
const bgs=[guowu,waijiaojigou,difangzhengf]
// 第六屏幕 弹幕加treemap
const drawScreen6 = function (addTitle,duration, svg, windowWidth, windowHeight,windowIndex) {
    
    let title_g=svg.append("g")
        .attr("class","sub-title")
    svg.style( "background-image",`url("${bgs[windowIndex-6]}")`)
    addTitle(title_g,titles[windowIndex-6], titlesLength[windowIndex-6], true)
    
    
    let showData=[Frequency1.filter(d=>d.frequency>=filterValue),
         countryData.filter(d=>d.frequency>=filterValue), 
         regionalData.filter(d=>d.frequency>=filterValue)
        ][windowIndex-6]
    let titleSize = windowWidth/16
    let rectBarG = svg.append("g")
        .attr("class", "treemap-g")
        .attr("opacity", 1)
    
    rectBarG.append("g")
        .attr("class","backrgound")
        .append("rect")
        .transition()
        .duration(duration)
        .attr("x", windowWidth*0.05)
        .attr("y",()=>{
            return windowHeight*0.15
        })
        .attr("width",()=>{
            // if(windowIndex===8){
            //     return windowWidth*0.5
            // }else{
                return windowWidth*0.9
            // }
        })
        .attr("height",()=>{
            // if(windowIndex===8){
            //     return windowHeight * 0.85
            // }
            return windowHeight*0.5
        })
        .attr("fill",rgb(55,121,60))
    rectBarG.append("g")
        .attr("class","local-title-desc")
        .append("text")
        .attr("fill","#FFF")
        .transition()
        .duration(duration)
        .attr("transform", (d, i) => {
            return `translate(${0.1 * windowWidth},
                ${
                    // windowIndex===8?
                    // windowHeight * 0.95 
                    // : 
                    windowHeight*0.65
                })`
        })
        .attr("font-size", titleSize*0.5)
        .attr("text-anchor", "start")
        .attr("alignment-baseline", "bottom")
        .attr("dy","-1em")
        .text(`注：只显示词频>=${filterValue}次文字`)
    
    // rectBarG.append("g")
    //     .attr("class","local-title")
    //     .append("text")
    //     .attr("transform", (d, i) => {
    //         return `translate(${0.5 * windowWidth},${windowHeight * 0.05})`
    //     })
    //     .attr("class", "who-answer")
    //     .attr("font-size", titleSize)
    //     .attr("text-anchor", "middle")
    //     .attr("alignment-baseline", "hanging")
    //     .attr("dy","0.2em")
    //     .text(titles[windowIndex-6])
    // treemap
    let width=windowWidth*(
        //windowIndex===8?0.4:
        0.8)
    let height=windowHeight*(
        // windowIndex===8?0.8:
        0.45)
    let treemap = data => d3.treemap()
            .size([width, height])
            .padding(1)
            .round(true)(d3.hierarchy(data)
            .sum(d => d['frequency'])
            .sort((a, b) => b['frequency'] - a['frequency']))
    const root = treemap({ name: "", children: showData });
    const leaf = rectBarG.append("g")
        .attr("class","treemap-local-g")
        .attr("transform",`translate(${windowWidth*0.1},${windowHeight*0.15})`)
        .selectAll("g")
        .data(root.leaves())
        .join("g")
    leaf.transition()
        .duration(duration)
        .attr("transform", d => {
            let maxLength = d.data.word.length
            let width = d.x1 - d.x0
            let height = d.y1 - d.y0
            d.fontSize = Math.min(width/maxLength, height/maxLength)
            return `translate(${d.x0},${d.y0})`
        });

    leaf.append("title")
        .text(d => `${d.data.word}\n${d.data['frequency']}`);

    leaf.append("rect")
        .attr("id", d => (d.leafUid = d.data.word+"leafUid"))
        .attr("fill", rgb(111,175,124))
        .attr("fill-opacity", 0.6)
        .attr("width", d => d.x1 - d.x0)
        .attr("height", d => d.y1 - d.y0);

    leaf.append("clipPath")
        .attr("transform",d=>`translate(${-(d.x1 - d.x0)/2},${-(d.y1 - d.y0)/2})`)

        .attr("id", d => (d.clipUid = d.data.word+"clipUid"))
        .append("use")
        .attr("xlink:href", d => "#"+d.leafUid);

    leaf.filter(d=>{
            return d.value>=filterValue
        })
        .append("text")
        .attr("fill","#FFF")
        .attr("font-size",(d)=>d.fontSize)
        .attr("clip-path", d => `url(#${d.clipUid})`)
        .attr("transform",d=>`translate(${(d.x1 - d.x0)/2},${(d.y1 - d.y0)/2})`)
        .attr("text-anchor","middle")
        .attr("alignment-baseline","middle")
        .selectAll('tspan')
        .data(d=>{
            if((d.x1 - d.x0)<(d.y1 - d.y0)){
                return d.data.word.split('').map(a=>{
                    return {value: a, data: d}
                })
            }else{
                return [{value:d.data.word, data:d}]
            }
        })
        .join("tspan")
        .attr("x", 0)
        .attr("y", (d,i,data)=>{
            let length = data.length
            return d.data.fontSize*(1/2 + i - (length-1)/2)
        })
        .attr("fill-opacity", (d, i, nodes) => i === nodes.length - 1 ? 0.9 : null)
        .text((d,i) => {
            return d.value
        });
    return rectBarG
}
